import React from "react";



const Modals=({active,setActive, children})=>{

    return(    
    <>
        <div className={active ? "modals active" : "modals"}  onClick={()=> setActive(false)}>
            <div className="modals__content"  onClick={e => e.stopPropagation()}>
                {children}
            </div>
    
        </div>

    </>


    )


}
export default Modals;